// When true (checked)
:host
  ::ng-deep
  .changeable-label-mat-slide-toggle.mat-mdc-slide-toggle.mat-mdc-slide-toggle-checked:not(.mat-disabled)
  .mdc-switch__shadow {
  background-color: #0088f4;
}

:host
  ::ng-deep
  .changeable-label-mat-slide-toggle.mat-mdc-slide-toggle.mat-mdc-slide-toggle-checked:not(.mat-disabled)
  .mdc-switch__track::after {
  background-color: #7fc3f9 !important;

}

// When false (unchecked)
:host
  ::ng-deep
  .changeable-label-mat-slide-toggle.mat-mdc-slide-toggle:not(.mat-mdc-slide-toggle-checked):not(.mat-disabled)
  .mdc-switch__shadow {
  background-color: #7fc3f9;

}

:host
  ::ng-deep
  .changeable-label-mat-slide-toggle.mat-mdc-slide-toggle:not(.mat-mdc-slide-toggle-checked):not(.mat-disabled)
  .mdc-switch__track::before {
  background-color: #bfe1fc !important;

}

.eav-slide-toggle-label {
  margin-left: 8px;
}

.eav-toggle-hitbox {
  width: fit-content;
  cursor: pointer;
}

.eav-toggle-label {
  color: grey;
  cursor: default;
}
